@import "fonts.scss";

$bred: 0.5px solid red;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border-radius: 0%;
  border: none;
  outline: none;
}

body {
  // border: $bred;
  color: #011C40;
  font-family: "Montserrat", "Arial", sans-serif;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.visually-hidden {
  display: none;
}

button:focus {
  box-shadow: inset 0px 0px 1px 3px rgba(29, 65, 1, 0.753);
}

button:hover {
  color: #7b6be4;
}

button:active {
  color: #7b6be4;
  /* offset-x | offset-y | blur-radius | spread-radius | color */
  box-shadow: inset 0px 0px 5px 5px rgba(45, 39, 124, 0.425), inset 0px 0px 5px 5px rgba(45, 39, 124, 0.425);
}

button:disabled {
  background: rgba(130, 134, 125, 0.301);
  color: #353538;
}


@import "header.scss";
@import "acquaintance.scss";
@import "advantages.scss";
@import "early-cruises.scss";
@import "booking.scss";
@import "address.scss";
@import "footer.scss";


input:invalid:not(:focus):not(:placeholder-shown) {
  background-color: rgba(238, 121, 121, 0.89);
}

